import React from "react";
import { Route, Routes } from "react-router-dom";
import Header from "./components/layout/Header";
import Container from "./components/common/Container";
import Hero from "./components/layout/Hero";
import Navigation from "./components/layout/Navigation";
import { Latest, Performance, Portfolio, Reviews, Summary } from "./components";

function App() {
  return (
    <Container>
      <Header />
      <Hero />
      <Navigation />
      <div className="wrapper">
        <Routes>
          <Route path="/development-task/summary" element={<Summary />} />
          <Route
            path="/development-task/performance"
            element={<Performance />}
          />
          <Route path="/development-task/portfolio" element={<Portfolio />} />
          <Route path="/development-task/reviews" element={<Reviews />} />
          <Route path="/development-task/latest" element={<Latest />} />
        </Routes>
      </div>
    </Container>
  );
}

export default App;
